:root {
  font-size: 24px;
}

body {
  background: linear-gradient(to left, #283593, #1976d2);
  height: 100vh;
  font-family: Josefin Sans, sans-serif;
}

input {
  color: #fff;
  background-color: #0000;
  border: 1px solid #000;
  outline: none;
}

input::placeholder {
  color: #fff;
}

input.not-valid {
  border: 1px solid red;
}

.circle {
  background: radial-gradient(#006db3, #29b6f6);
  border-radius: 50%;
  position: absolute;
}

.circles {
  width: 450px;
  height: 270px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle-1 {
  width: 180px;
  height: 180px;
  top: -50px;
  left: -60px;
}

.circle-2 {
  opacity: .8;
  width: 200px;
  height: 200px;
  bottom: -90px;
  right: -90px;
}

.card-group {
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card {
  backdrop-filter: blur(30px);
  background: #fff3;
  border: 2px solid #ffffff1a;
  border-radius: 25px;
  width: 450px;
  height: 270px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 80px #0003;
}

.logo, .cvc, .chip img, .number, .email, .from, .to, .ring {
  position: absolute;
}

.logo {
  color: #fffc;
  width: 80px;
  top: 35px;
  right: 0;
}

.cvc {
  color: #fffc;
  align-items: center;
  display: flex;
  top: 35px;
  left: 30px;
}

.cvc input {
  max-width: 50px;
  margin-left: 10px;
}

.chip img {
  opacity: .8;
  width: 50px;
  height: auto;
  top: 120px;
  left: 40px;
}

.number, .email, .from, .to {
  color: #fffc;
  letter-spacing: 2px;
  text-shadow: 0 0 2px #0009;
  font-weight: 400;
}

.number {
  width: 175px;
  font-family: Nunito, sans-serif;
  bottom: 65px;
  left: 40px;
}

.email {
  font-size: .5rem;
  bottom: 35px;
  left: 40px;
}

.from {
  max-width: 50px;
  font-size: .5rem;
  bottom: 35px;
  right: 110px;
}

.to {
  max-width: 37px;
  font-size: .5rem;
  bottom: 35px;
  right: 40px;
}

.ring {
  box-sizing: border-box;
  background: none;
  border: 50px solid #ffffff1a;
  border-radius: 50%;
  width: 500px;
  height: 500px;
  bottom: -250px;
  right: -250px;
}

.ring:after {
  content: "";
  box-sizing: border-box;
  background: none;
  border: 30px solid #ffffff1a;
  border-radius: 50%;
  width: 600px;
  height: 600px;
  position: absolute;
  bottom: -80px;
  right: -110px;
}

.btn-send {
  color: #fff;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 25px;
  align-self: center;
  width: 200px;
  margin-top: 30px;
  padding: 15px 10px;
  transition: all .2s ease-in-out;
}

.btn-send:hover {
  color: #8f8585;
  border: 1px solid #8f8585;
}

.btn-send:disabled {
  color: #000;
  border: 1px solid #000;
}
